//play button

.playbutton{
    background: $cta;
    border-radius: 50%;
    height: 4rem;
    width: 4rem;
    position: relative;
    margin: auto;
    top:0;
    bottom:0;
    right:0;
    left:0;
    -webkit-transition: background-color .3s ease, -webkit-box-shadow .3s ease;
    transition: background-color .3s ease, -webkit-box-shadow .3s ease;
    transition: box-shadow .3s ease, background-color .3s ease;
    transition: box-shadow .3s ease, background-color .3s ease, -webkit-box-shadow .3s ease;
    &:hover {
      background-color: $focus;
      -webkit-box-shadow: 0 8px 4px -6px $primaryShadow;
              box-shadow: 0 8px 4px -6px $primaryShadow;
    }
}
.playbutton:before{
    content: "";
    display: block;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 10px 0 10px 20px;
    border-color: transparent transparent transparent $light;
    position: absolute;
    top:0;
    left:0;
    right:-5px;
    bottom:0;
    margin: auto;
}


.fa-play-circle::before {
  background-color: $cta;
  height: 80%;
  width: 80%;
  border-radius: 50%;
}

.fa-play-circle {
  color:$light;
}

a.fa-ellipsis-h {
  text-decoration: none;
  color: $cta;
  -webkit-transition: color .3s ease, text-shadow .3s ease;
  transition: color .3s ease, text-shadow .3s ease;
  &:hover {
    color: $focus;
      text-shadow: 1px 1px 2px $primaryShadow;
  }
}

.fa-ellipsis-h {
  color: $cta;
  -webkit-transition: color .3s ease, text-shadow .3s ease;
  transition: color .3s ease, text-shadow .3s ease;
  &:hover {
    color: $focus;
      text-shadow: 1px 1px 2px $primaryShadow;
  }
}

.lyric-filter {
  border-radius: 4rem;
  padding: 0 2rem;
  cursor: pointer;
}
