h1, .h1 {
  @include fontStyles($primary, 700, center, normal);
  @include fluid-type(2rem, 4rem, 30rem, 75rem);
}

h2, .h2 {
  @include fontStyles($primary, 700, center, normal);
  @include fluid-type(1.5rem, 3rem, 30rem, 75rem);
}

h3, .h3 {
  @include fontStyles($primary, 700, center, normal);
  @include fluid-type(1.125rem, 2.5rem, 30rem, 75rem);
}

h4, .h4 {
  @include fontStyles($primary, 700, center, normal);
  @include fluid-type(1rem, 1.875rem, 30rem, 75rem);
}

h5, .h5 {
  @include fontStyles($primary, 700, center, normal);
  @include fluid-type(1rem, 1.625rem, 30rem, 75rem);
}

h6, .h6 {
  @include fontStyles($primary, 700, center, normal);
  @include fluid-type(.75rem, 1.25rem, 30rem, 75rem);
}

p {
  @include fontStyles($dark, 400, justify, normal, $secondaryTypeface);
  @include fluid-type(0.75rem, 1.125rem, 30rem, 75rem);
  a {
    color: $primary;
    &:hover {
      color: $focus;
    }
  }
}

figcaption {
  p {
    font-size: .75rem;
    text-align: center;
  }
}

footer {
  p {
    color: $light;
    font-size: .74rem;
  }
}


.jumbotron {

  h1 {
    @include fontStyles($light, 700, false, normal);
    text-shadow: 1px 1px 1px $primaryShadow;
  }
  h2 {
    @include fontStyles($light, 700, false, normal);
    text-shadow: 1px 1px 1px $primaryShadow;
  }
  .lead {
      @include fluid-type(1rem, 1.25rem, 30rem, 90rem);
  }
}

.lead {
  @include fluid-type(1rem, 1.5rem, 30rem, 90rem);
}

.inverse {
  color: $light;
}

.shadow {
    text-shadow: 1px 1px 1px $primaryShadow;
}

.text-center {
  text-align: center;
}
