// mobile first stylings

.card-array { //this is a ul element
  display: block;
  list-style: none;
  padding: 0;
  margin: 0;
  li {
    max-width: -webkit-max-content;
    max-width: -moz-max-content;
    max-width: max-content;
  }
}

.card-instance,
.card-instance a {
  text-decoration: none;
  padding: 0;
}

.card {
  margin: 0 auto;
  margin: 0;
  padding: 0;
  -webkit-transition: background-color, .3s ease;
  transition: background-color, .3s ease;
  img {
    -o-object-fit: cover;
       object-fit: cover;
  }
  figcaption {
    padding: 1rem;
    h2, p, i {
      line-height: 1rem;
    }
    h2 {
      margin-top: 0;
    }
    p {
      font-size: 1rem;
    }
  }
  &:hover {
    background-color: lighten($secondary, 25%);
  }
}

.playlist-card, .feature-card {
  width: 280px;
  height: 280px;
  img {
    width: 100%;
    height: 60%;
    -o-object-position: top;
       object-position: top;
  }
}

.pricing-card {
  width:22em;
  min-height: 30em;
  margin: .5rem;
  background-color: $light;
  &:hover {
    background-color: $light;
  }
  h2 {
    top: 0;
    padding: 1rem;
    margin: 0;
    color: $light;
    @include linearGradient(to top left, $tertiary, $primary, $primary);
  }
.card-details {
    padding: 1rem;
    h3 {
      background-color: transparent;
    }
      p {
        padding: 0 1rem;
        text-align: left;
      }
  }
}

@include mq('desktop') {
}


.on-light {
  -webkit-box-shadow: 1px 2px 2px transparentize($primaryShadow, .5);
          box-shadow: 1px 2px 2px transparentize($primaryShadow, .5);
}

.on-dark {
  -webkit-box-shadow: 1px 2px 2px $primaryShadow;
          box-shadow: 1px 2px 2px $primaryShadow;
}
