/*colour scheme*/
$primary: #6600cc;
$secondary: #9966cc;
$tertiary: #0066cc;
$primaryShadow: darken($primary, 20%);

//buttons
$cta: lighten($tertiary, 15%);
$focus: $tertiary;



//black and white
$dark: #000000;
$light: #ffffff;


//lyricFilter colors
$green: green;
$yellow: yellow;
$orange: orange;



$navBarColor: $light;

/*typography*/
$mainTypeface: 'proxima-nova', sans-serif;
$secondaryTypeface: 'open-sans', sans-serif;
$headingColor: $primary;
$bodyTextColor: $dark;
$invertTextColor: $light;


/*media breakpoints*/
$breakpoints: (
    "phone":        25em, //400px,
    "phone-wide":   30em, //480px,
    "phablet":      35em, //560px,
    "tablet-small": 40em, //640px,
    "tablet":       48em, //768px,
    "tablet-wide":  64em,//1024px,
    "desktop":      78em, //1248px,
    "desktop-wide": 90em, //1440px
);
