//mobile-first navigation
// put padding on the div following the nav element to eliminate space between nav and rest of content

nav  {
  margin-bottom: 0;
}

.navtoggle {
  position: fixed;
  z-index: 5;
  padding: 1rem;
  font-size: 1.5rem;
  right: 0;
  top: 0;
}

.navbar-brand {
  display: none;
}


/*animation for unexpanded nav toggle
@-webkit-keyframes rotate {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes rotate {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes rotate {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}*/

.fa-bars {
  color: $light;
  text-shadow: 1px 2px 1px $primaryShadow;
/*-webkit-animation-name: rotate;
       -webkit-animation-name: rotate;
               animation-name: rotate;
       -webkit-animation-delay: 5s;
               animation-delay: 5s;
       -webkit-animation-timing-function: ease-in-out;
               animation-timing-function: ease-in-out;
        -webkit-animation-direction: alternate;
               animation-direction: alternate;
       -webkit-animation-duration: 5s;
               animation-duration: 5s;
         -webkit-animation-iteration-count: 2;
               animation-iteration-count: 2;*/
}

.fa-times {
  color: $secondary;
  text-shadow: 1px 2px 1px $light;
}

.navigation {
  list-style: none;
  margin: 0;
  top: 0;
  background: transparentize($light, .2);
  padding: 0;
  position: -webkit-sticky;
  position: fixed;
  z-index: 1;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  -ms-flex-pack: distribute;
      -webkit-box-pack: center;
          justify-content: center;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-flow: column wrap;
              flex-flow: column wrap;
}

.navigation a {
  display: block;
  @include fontStyles($primary, 700, false, false);
  font-size: 1rem;
  text-align: center;
  text-decoration: none;
  padding: 1.75rem 0;
  border-bottom: 1px solid $primary;
  -webkit-transition: background-color .3s ease;
  transition: background-color .3s ease;
}


.navigation a:hover {
  background-color: $secondary;
  color: $light;
}

.navigation a:active {
  background-color: $primary;
  color: $light;
}

.navigation a.active {
  background-color: $primary;
  color: $light;
}

.hide-small {
  visibility: hidden;
}

@include mq('tablet-wide') { //min-width for full navbar 1024px

  .hide-small {
    visibility: visible;
  }

  .navigation {
    position: relative;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-flow: row wrap;
            flex-flow: row wrap;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
             -webkit-box-shadow: 1px 1px 1px $primaryShadow;
                   box-shadow: 1px 1px 1px $primaryShadow;
  }

  .navigation a {
    border-bottom: none;
    padding: 1.25rem;
  }

  .navbar-brand {
    display: block;
    @include fontStyles($primary, 700, false, false);
    font-size: 20px;
    text-decoration:none;
    position: absolute;
    z-index: 2;
    padding: 1.25rem;
    left: 0;
    top: 0;
  }
} // end of full-navbar at min-width: 1024px
