//mobile first styles

//title styled like jumbotron but not jumbotron

.jumbo-title {
  padding: .5rem 0;
  margin-bottom: 2.5rem;
  width: 100%;
  @include linearGradient(to top left, $tertiary, $primary, $secondary);
}


.jumbotron {
  height: 70vmin;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  padding: 4rem 2rem;
  @include linearGradient(to top left, $tertiary, $primary, $secondary);
  img {
    display: none;
  }
  h1 {
  }
}

@include mq('tablet') {

  .jumbotron {
    height: 40vh;
    overflow-y: hidden !important;
    img {
      display: block;
      opacity: .4;
      -o-object-fit: cover;
         object-fit: cover;
         -o-object-position: top;
            object-position: top;
      width: 100%;
      height: 40vh;
      position: absolute;
      z-index: 2;
    }
    h1, h2, a {
      position: relative;
      z-index: 3;
    }
  }

}
