.carousel-wrapper {
//  @include linearGradient(to top left, $tertiary, $primary, $secondary);
  position: relative;
  max-width: 1200px;
  padding: 0 .5rem 2rem .5rem;
  margin: 0 auto;
}

.carousel-array {
  max-width: 1180px;
  padding-left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  list-style: none;
  overflow-x: scroll;
  overflow-y:hidden;
  &::-webkit-scrollbar {
    display: none;
  }
}

.carousel-item {
  background-color: $light;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 auto;
          flex: 0 0 auto;
          margin: 0rem .5rem !important;
}

.carousel-control {
  top:45%;
  padding: 1rem;
  color: $light;
  background-color: $cta;
  &:hover {
    cursor: pointer;
  }
}

.carousel-prev {
  position: absolute;
  left: 0;
}

.carousel-next {
  position: absolute;
  right: 0;
}

@include mq('tablet-wide', 'max') {
  .carousel-control {
    top: 0%;
  }
}
