// mobile first stylings with no grid
.wrapper {
  padding: 0 1rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
          -ms-flex-wrap: wrap;
              flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.content {
  p {
    text-align: center;
    padding: 0rem .625rem;
  }
}

.featured {
  @include linearGradient(to top left, $tertiary, $primary, $secondary);
  padding-top: 1rem;
}

.row {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}

.space-between {
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.card-array {
  margin: 0 auto;
}

.playlist-card {
  margin: .5rem;
}


.single-playlist-wrapper {
  img {
    -o-object-fit: cover;
       object-fit: cover;
       -o-object-position: top;
          object-position: top;
  }
}

.business-music-plans {
  background-image: url("../images/business/small-business-owner-has-legal-business-music.png");
  background-size: cover;
  padding-bottom: 2rem;
  h3 {
    background-color: $secondary;
    width: 100%;
    padding: 1rem;
  }
}

.hardware-option {
  padding: 2rem 0;
  width: 100%;
  background-image: url("../images/business/good-business-music-statistics.png");
  background-size: cover;
}


//grid and wider devices start below

