      grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
      grid-auto-rows: 300px;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center; */
              display: -webkit-box;
              display: -ms-flexbox;
              display: flex;
              -webkit-box-orient: horizontal;
              -webkit-box-direction: normal;
                  -ms-flex-direction: row;
                      flex-direction: row;
              -ms-flex-wrap: wrap;
                  flex-wrap: wrap;
    }

  }// minimum width of 768px ends

  @include mq('tablet-wide') { // minimum width of 1024px

  } // min-width 1024px ends

  @include mq('desktop') {
    .wrapper {
      margin: 0 auto;
      max-width: 1248px;
      // interim flex layouts to make desktop at least somehwat ok
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
